const API_URL = "http://localhost:8000";

async function httpGetPlanets() {
    const planets = await fetch(`${API_URL}/planets`, { method: "GET" });
    const data = await planets.json();
    return data;
}

async function httpGetLaunches() {
    const launches = await fetch(`${API_URL}/launches`, { method: "GET" });
    const launchesData = await launches.json();
    return launchesData.sort((a, b) => {
        return a.flightNumber - b.flightNumber;
    });
}

async function httpSubmitLaunch(launch) {
    try {
        const launches = await fetch(`${API_URL}/launches`, {
            method: "POST",
            headers: { "Content-Type": "application/json" },
            body: JSON.stringify(launch),
        });
        const launchesData = await launches.json();
        return launchesData;
    } catch (error) {
        console.log("ERORr", error);
        return {
            message: null,
            response: false,
        };
    }
}

async function httpAbortLaunch(id) {
    try {
        const launches = await fetch(`${API_URL}/launches/${id}`, {
            method: "delete",
        });
        const launchesData = await launches.json();
        return launchesData;
    } catch (error) {
        return {
            success: false,
        };
    }
}

export { httpGetPlanets, httpGetLaunches, httpSubmitLaunch, httpAbortLaunch };
